.slider-image-back {
  height: 90vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.im1 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/mainpage-slider1.jpg");
}

.im2 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/mainpage-slider2.jpg");
}

.im3 {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/mainpage-slider3.jpg");
}

.text-color {
  text-align: center;
  vertical-align: middle;
  color: white;
}

.text-color h1 {
  vertical-align: middle;
  position: relative;
  top: 40%;
}

.header-wrapper {
  position: relative;
  top: 30%;
  bottom: 50%;
}
