.hero-image-header {
  padding-top: 100px;
  padding-bottom: 100px;
  margin-bottom: 0px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/about-us-heroimage.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.jumbotron-contact {
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.hero-image-desktop-version-contact {
  height: 25rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1600x500.jpg");
    padding-top: 15vh;
    padding-bottom: 15vh;
}

.hero-image-mobile-version-contact {
  height: 15rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1200x800.jpg");
}

.hero-image-contacts {
  color: white;
}

.col-color {
  background-color: #dddddd;
}

.form-wrapper-new {
  position: relative;
}

.contact-form-new {
  background-color: #eeeeee;
}

.col2-color {
  background-color: #ffffff;
}

.styled-row {
  margin-top: 10rem;
  margin-bottom: 10rem;
}

.contact-info-stats {
  text-align: left;
  position: relative;
}

@media only screen and (min-width: 960px) {
  .styled-row {
  margin-top: 10rem;
  margin-bottom: 10rem;
}

}
@media only screen and (max-width: 960px) {
  .styled-row {
  margin-top: 3rem;
  margin-bottom: 3rem;

}

  .contact-info-stats {
    margin-top: 10rem;
    margin-bottom: 10rem;
  }
}



