.hero-image-shop-header {
  padding-top: 100px;
  padding-bottom: 100px;
  margin-bottom: 50px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/about-us-heroimage.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.jumbotron-shop {
  margin-bottom: 50px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.hero-image-desktop-version-shop {
  height: 25rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1600x500.jpg");
    padding-top: 15vh;
    padding-bottom: 15vh;
}

.hero-image-mobile-version-shop {
  height: 15rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1200x800.jpg");
}


.hero-image-shop {
  color: white;
}

.header-text {
  margin-bottom: 50px;
}

.aboutpage-wrapper {
  margin-bottom: 50px;
}



