.hero-image-header {
  margin-bottom: 50px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8)),
    url("./media/about-us-heroimage.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.jumbotron-about {
  margin-bottom: 50px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  position: relative;
}

.hero-image-desktop-version-about {
  height: 25rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1600x500.jpg");
    padding-top: 15vh;
    padding-bottom: 15vh;
}

.hero-image-mobile-version-about {
  height: 15rem;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url("./media/heroimage-about-1200x800.jpg");
}

.hero-image {
  color: white;
}

.header-text {
  margin-top: 50px;
  margin-bottom: 50px;
}

.aboutpage-wrapper {
  margin-bottom: 50px;
}

.about-text-wrapper {
  margin-top: 5rem;
  margin-bottom: 5rem;
}


